import con from './config'
import axios from 'axios'
const state = {
  users: [],
  currentRowNo: 0,
  access: [],
  iisEmployee: []
}

const getters = {
  allUsers: state => state.users,
  getCurrenrow: state => state.currentRowNo,
  allAccess: state => state.access,
  allIisEmployee: state => state.iisEmployee
}

const actions = {
  // for next button
  async fetchUsers({ commit, rootGetters }, row) {
    let iis_employee_number = row.iis_employee_number;
    await con.get(`/Users/${row.limit}/${row.offset}/${iis_employee_number}`).then(function (res) {
      if (res.data['Users'].length > 0) {
        // commit data until last

        commit('getUsers', isSuperAdmin(res.data['Users'], rootGetters['getStateSession']));

        // console.log(newState['super_admin'])

      } else {
        // minus 10 if last data meet
        var rowoffset = row.offset - row.limit;
        if (rowoffset <= 0) {
          rowoffset = 0;
        }
        commit('currentRow', rowoffset);
      }

    }).catch(error => {
      if (error.response.data.error == "Expired token") {
        sessionStorage.clear();
        window.location.href = "https://iis.emb.gov.ph/embis/";
      }
    })
  },
  async searchUsers({ commit, rootGetters }, user) {
    let iis_employee_number = parseInt(user.iis_employee_number);
    var limit = parseInt(user.limit);
    await con.post('/SearchClient', {
      txtsearch: user.txtsearch,
      txtlimit: limit,
      iis_employee_number: iis_employee_number
    }).then(function (res) {
      commit('currentRow', res.data['offset']);
      commit('getUsers', isSuperAdmin(res.data['Users'], rootGetters['getStateSession']));
    }).catch(error => {
      if (error.response.data.error == "Expired token") {
        sessionStorage.clear();
        window.location.href = "https://iis.emb.gov.ph/embis/";
      }
    })
  },
  // for next button increment only
  async increment({ commit }, row) {
    var limitoffset = parseInt(row.offset) + parseInt(row.limit);
    commit('currentRow', limitoffset);
  },
  async decrement({ commit }, row) {
    // decrement
    var limitoffset = parseInt(row.offset) - parseInt(row.limit);
    commit('currentRow', limitoffset);
    if (row.offset <= 0) {
      // commit to 0 if data is less than zero
      commit('currentRow', 0);
    }
  },
  async createUser({ commit, rootGetters }, user) {
    var id = user.id;
    await con.post(`/Users/${user.limit}/${user.offset}/${id}/${user.origin}`,
      {
        region: user.region,
        name: user.name,
        admin: user.admin,
        user_access_level: user.user_access_level.value,
        iis_employee_number: user.iis_employee_number,
        origin: user.origin
      }).then(function (res) {
        commit('getUsers', isSuperAdmin(res.data['Users'], rootGetters['getStateSession']));
        swal({
          title: "Success",
          icon: "success",
        })

      }).catch(error => {
        if (error.response.data.error == "Expired token") {
          sessionStorage.clear();
          window.location.href = "https://iis.emb.gov.ph/embis/";
        } else {
          console.log(error.response.data['iis_employee_number'])
          var errorLog = JSON.stringify(error.response.data);
          let error1 = ""
          if (error.response.data['user_access_level']) {
            error1 = "User Type fields is Required!"
          } else if (error.response.data['iis_employee_number']) {
            error1 = "User is already exist"
          }
          swal({
            title: error1,
            // text: error1,
            icon: "error",
            dangerMode: true,
          });
        }
      })

  },
  async updateUsers({ commit, rootGetters }, user) {
    const res = await con.put(`/Users/${user.id}`, {
      // name: user.name,
      user_access_level: user.user_access_level,
      active: user.active,
      admin: user.admin,
      offset: user.offset,
      limit: user.limit,
      iis_employee_number: user.editorId,
      group: user.group,
    }).then(function (res) {
      commit('getUsers', isSuperAdmin(res.data['Users'], rootGetters['getStateSession']));
      swal({
        title: "Updated",
        icon: "success",
      })

    }).catch(error => {
      if (error.response.data.error == "Expired token") {
        sessionStorage.clear();
        window.location.href = "https://iis.emb.gov.ph/embis/";
      }
    })

  },
  async deleteUsers({ commit, rootGetters }, user) {
    var iis_employee_number = user.iis_employee_number;
    const res = await con.delete(`/Users/${user.limit}/${user.offset}/${user.id}/${iis_employee_number}`).catch(error => {
      if (error.response.data.error == "Expired token") {
        sessionStorage.clear();
        window.location.href = "https://iis.emb.gov.ph/embis/";
      }
    })
    commit('getUsers', isSuperAdmin(res.data['Users'], rootGetters['getStateSession']));
  },

  async fetchAccess({ commit }) {
    await con.get(`/Access`).then(function (res) {
      var access = [];
      for (let i = 0; i < res.data['Access'].length; i++) {
        access[i] = {
          value: res.data['Access'][i]['id'],
          text: res.data['Access'][i]['access_level']
        }
      }

      // access = access.filter((e) => !(e.value === '1'))
      commit('getAccess', access);

    }).catch(error => {
      console.log(error);
      if (error.response.data.error == "Expired token") {
        sessionStorage.clear();
        window.location.href = "https://iis.emb.gov.ph/embis/";
      }
    })
  },

  async getIssEmployee({ commit }, data) {
    let region = data.region
    let super_admin = data.super_admin

    if (data.name != "") {
      await con.get(`/GetiisEmployee/${data.name}/${data.origin}`).then(function (res) {
        if (res.data.length > 0) {
          let new_data = ""
          if (super_admin != 1) { // If not super admin, it filter depends on users region
            new_data = res.data.filter((e) => e.regid === region)
          } else {
            new_data = res.data
          }
          console.log(new_data)
          commit('iisEmployees', new_data);
        } else {
          commit('iisEmployees', []);

        }


      }).catch(error => {
        console.log(error);
        if (error.response.data.error == "Expired token") {
          sessionStorage.clear();
          window.location.href = "https://iis.emb.gov.ph/embis/";
        }
      })
    }

  },

};

const mutations = {
  getUsers: (state, User) => (state.users = User),
  currentRow: (state, Irow) => (state.currentRowNo = Irow),
  getAccess: (state, Access) => (state.access = Access),
  iisEmployees: (state, iis) => (state.iisEmployee = iis)
}

function isSuperAdmin(data, newState) {
  let region = newState['region_id'];
  let super_admin = newState['super_admin'];
  // const sessionStateStorage = store.getters['iisTokenApi/getStateSession'];
  // console.log(sessionStateStorage)
  let new_data = ""
  if (super_admin != 1) { // If not super admin, it filter depends on users region
    new_data = data.filter((e) => e.region === region)
  } else {
    new_data = data
  }

  return new_data
}

export default {
  state,
  getters,
  actions,
  mutations
};