import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/user',
    name: 'User',
    component: () => import(/* webpackChunkName: "about" */ '../views/User.vue')
  },
  {
    path: '/res',
    name: 'Responsibility',
    component: () => import(/* webpackChunkName: "about" */ '../views/Res.vue')
  },
  {
    // /:chapter?
    path: '/plan',
    name: 'Plan',
    component: () => import(/* webpackChunkName: "about" */ '../views/Plan.vue')
  },
  {
    path: '/eplan',
    name: 'EMBPlanList',
    component: () => import(/* webpackChunkName: "about" */ '../views/EPlan.vue')
  },
  {
    path: '/lguPro',
    name: 'LGUProfile',
    component: () => import(/* webpackChunkName: "about" */ '../views/Profile.vue')
  },
  {
    path: '/land',
    name: 'LandClass',
    component: () => import(/* webpackChunkName: "about" */ '../views/LClass.vue')
  },
  {
    path: '/nature',
    name: 'NatureBusiness',
    component: () => import(/* webpackChunkName: "about" */ '../views/NBusiness.vue')
  },
  {
    path: '/evaluation',
    name: 'Evaluation',
    component: () => import(/* webpackChunkName: "about" */ '../views/Evaluation.vue')
  },
  {
    path: '/formtwo/:id?',
    name: 'FormTwoViews',
    component: () => import(/* webpackChunkName: "about" */ '../views/FormTwoViews.vue')
  },
  {
    path: '/formthree/:id?',
    name: 'FormThreeViews',
    component: () => import(/* webpackChunkName: "about" */ '../views/FormThreeViews.vue')
  },
  {
    path: '/review',
    name: 'Review',
    component: () => import(/* webpackChunkName: "about" */ '../views/Review.vue')
  },
  {
    path: '/deliberate',
    name: 'Deliberate',
    component: () => import(/* webpackChunkName: "about" */ '../views/Deliberate.vue')
  },
  {
    path: '/query',
    name: 'Query',
    component: () => import(/* webpackChunkName: "about" */ '../views/Query.vue')
  },
  {
    path: '/print',
    name: 'Print',
    component: () => import(/* webpackChunkName: "about" */ '../views/PrintView.vue')
  },

  {
    path: '/nswmcEval',
    name: 'NswmcEval',
    component: () => import(/* webpackChunkName: "about" */ '../views/NswmcEval.vue')
  },

]

const router = new VueRouter({
  routes
})

export default router
