  // TABLE OTHERS API
  async fetchPlanList({ commit }, row) {
    //NOTE: Will change var using session
    let user_type = row.user_type
    let user_id = row.user_id
    let regionId = row.regionId
    let group = row.group
    let action = "";
    row.action == undefined ? action = "" : action = row.action
    let city = "";
    if (user_type == 'lgu') {
      city = localStorage.getItem("city_id")
    }
    await con.post(`/PlanList/${row.limit}/${row.offset}/${regionId}`, {
      status: row.status,
      user_id: user_id,
      user_type: user_type,
      new_app: row.new_app,
      action: action,
      group: group,
      city: city,
    }).then(function (res) {
      if (res.data['PlanList'].length > 0) {
        commit('getPlanList', res.data['PlanList']);
      } else {
        commit('getPlanList', []);
        var rowoffset = row.offset - row.limit;
        if (rowoffset <= 0) {
          rowoffset = 0;
        }
        commit('currentRow', rowoffset);
      }

    }).catch(error => {
      console.log(error);
      if (error.response.data.error == "Expired token") {
        sessionStorage.clear();
        window.location.href = "https://iis.emb.gov.ph/embis/";
      }
      if (error.response.data.error == "Key may not be empty") {
        window.location.reload();
        commit('getPlanList', []);
      }
    })
  },
  async increment({ commit }, row) {
    var limitoffset = parseInt(row.offset) + parseInt(row.limit);
    commit('currentRow', limitoffset);
  },
  async decrement({ commit }, row) {
    // decrement
    var limitoffset = parseInt(row.offset) - parseInt(row.limit);
    commit('currentRow', limitoffset);
    if (row.offset <= 0) {
      // commit to 0 if data is less than zero
      commit('currentRow', 0);
    }
  },
  async searchPlanList({ commit }, res) {
    var limit = parseInt(res.limit);
    let user_type = res.user_type
    let user_id = res.user_id
    let regionId = res.regionId
    let group = res.group

    let action = "";
    res.action == undefined ? action = "" : action = res.action

    let city = "";
    if (user_type == 'lgu') {
      city = localStorage.getItem("city_id")
    }
    await con.post('/SearchPlanList', {
      txtsearch: res.txtsearch,
      txtlimit: limit,
      region: regionId,
      status: res.status,
      user_id: user_id,
      user_type: user_type,
      new_app: res.new_app,
      action: action,
      group: group,
      city: city,
    }).then(function (res) {
      commit('currentRow', res.data['offset']);
      commit('getPlanList', res.data['PlanList']);
    }).catch(error => {
      if (error.response.data.error == "Expired token") {
        sessionStorage.clear();
        window.location.href = "https://iis.emb.gov.ph/embis/";
      }
    })
  },
