import con from './config'
import swal from 'sweetalert'
const state = {
    Profile: [],
    Population: [],
}

const getters = {
    allProfile: state => state.Profile,
    allModPopul: state => state.Population,
}

const actions = {
    async fetchProfile({ commit }, row) {
        await con.get(`/FetchProfile/${row.user}/${row.lgu}`).then(function (res) {

            if (res.data['Profile'] != null) {
                commit('setProfile', res.data['Profile']);
            }

        }).catch(error => {
            console.log(error);
            if (error.response.data.error == "Expired token") {
                sessionStorage.clear();
                window.location.href = "https://iis.emb.gov.ph/embis/";
            }
        })
    },

    async fetchModPopulation({ commit }, id) {
        await con.get(`/GetModPopulation/${row.user}/${row.lgu}`).then(function (res) {
            commit('assignPopulation', JSON.stringify(res.data['Result']));
        }).catch(error => {
            if (error.response.data.error == "Expired token") {
                sessionStorage.clear();
                window.location.href = "https://iis.emb.gov.ph/embis/";
            }
        })

    },

    async saveProfile2({ commit }, res) {

        var table1 = JSON.parse(res.bar_popul).map((e, i) => ({
            ...e,
            totalPopulation: parseInt(e.totalPopulation.replace(/\,/g, '')),
            numberHousehold: parseInt(e.numberHousehold.replace(/\,/g, ''))
        }))

        var table2 = JSON.parse(res.proj_bar_popul).map((e, i) => ({
            ...e,
            column0: parseInt(e.column0.replace(/\,/g, '')),
            column1: parseInt(e.column1.replace(/\,/g, '')),
            column2: parseInt(e.column2.replace(/\,/g, '')),
            column3: parseInt(e.column3.replace(/\,/g, '')),
            column4: parseInt(e.column4.replace(/\,/g, '')),
            column5: parseInt(e.column5.replace(/\,/g, '')),
            column6: parseInt(e.column6.replace(/\,/g, '')),
            column7: parseInt(e.column7.replace(/\,/g, '')),
            column8: parseInt(e.column8.replace(/\,/g, '')),
            column9: parseInt(e.column9.replace(/\,/g, ''))
        }))

        await con.post('/CreateFrofile',
            {
                user: res.user,
                lgu: res.lgu,
                growth_rate: res.growth_rate,
                bar_popul: res.bar_popul,
                proj_bar_popul: res.proj_bar_popul,
                list_industries_within_city: res.list_industries_within_city,
                land_used_map_urban: res.land_used_map_urban,
                land_used_map_general: res.land_used_map_general,
            }).then(function () {
                swal({
                    title: "Save Successfully",
                    icon: "success",
                })
                // commit("chapter", 2);
            }).catch(error => {
                if (error.response.data.error == "Expired token") {
                    sessionStorage.clear();
                    window.location.href = "https://iis.emb.gov.ph/embis/";
                } else {
                    var errorLog = JSON.stringify(error.response.data);
                    swal({
                        title: "Server Side Validation",
                        text: errorLog,
                        icon: "error",
                        dangerMode: true,
                    }).then((willContinue) => {
                        if (willContinue) {
                            commit("chapter", 1);
                        }
                    });
                }
            })

    },

    async saveProfile4({ commit }, res) {

        await con.post('/CreateFrofile',
            {
                user: res.user,
                lgu: res.lgu,
                wacs: res.wacs,
                tbl_431: res.tbl_431,
                tbl_432: res.tbl_432,
            }).then(function () {
                swal({
                    title: "Save Successfully",
                    icon: "success",
                })
                // commit("chapter", 2);
            }).catch(error => {
                if (error.response.data.error == "Expired token") {
                    sessionStorage.clear();
                    window.location.href = "https://iis.emb.gov.ph/embis/";
                } else {
                    var errorLog = JSON.stringify(error.response.data);
                    swal({
                        title: "Server Side Validation",
                        text: errorLog,
                        icon: "error",
                        dangerMode: true,
                    }).then((willContinue) => {
                        if (willContinue) {
                            commit("chapter", 1);
                        }
                    });
                }
            })

    },

    async saveProfile11({ commit }, res) {

        await con.post('/CreateFrofile',
            {
                user: res.user,
                lgu: res.lgu,
                tbl_1111: res.tbl_1111,
                tbl_1121: res.tbl_1121,
                tbl_1122: res.tbl_1122,
                tbl_1131: res.tbl_1131,
                tbl_1141: res.tbl_1141,
                tbl_1151: res.tbl_1151,
            }).then(function () {
                swal({
                    title: "Save Successfully",
                    icon: "success",
                })
                // commit("chapter", 2);
            }).catch(error => {
                if (error.response.data.error == "Expired token") {
                    sessionStorage.clear();
                    window.location.href = "https://iis.emb.gov.ph/embis/";
                } else {
                    var errorLog = JSON.stringify(error.response.data);
                    swal({
                        title: "Server Side Validation",
                        text: errorLog,
                        icon: "error",
                        dangerMode: true,
                    }).then((willContinue) => {
                        if (willContinue) {
                            commit("chapter", 1);
                        }
                    });
                }
            })

    },


};

const mutations = {
    setProfile: (state, res) => (state.Profile = res),
    assignPopulation: (state, res) => (state.Population = res),
}

export default {
    state,
    getters,
    actions,
    mutations
};