<template>
  <v-app>
    <!--  NAV BAR-->
    <v-navigation-drawer
      v-model="drawer"
      :width="224"
      color="#08507e"
      app
      id="sidebar"
    >
      <v-list-item class="px-2">
        <v-list-item-avatar>
          <v-img src="favicon.ico"></v-img>
        </v-list-item-avatar>
        <v-list-item-content>
          <v-list-item-title class="title white--text">
            Welcome
          </v-list-item-title>
          <!-- <v-list-item-subtitle class="white--text text-body-2">
            {{ user_name }}
          </v-list-item-subtitle> -->
        </v-list-item-content>
      </v-list-item>

      <v-divider></v-divider>

      <v-list dense nav>
        <template v-for="item in newAllLink()">
          <v-list-item
            v-if="item.children == 0"
            :key="item.title"
            :to="item.to"
            link
            dense
            color="indigo lighten-3"
          >
            <v-list-item-icon>
              <v-icon color="white">{{ item.icon }}</v-icon>
            </v-list-item-icon>

            <v-list-item-content>
              <v-list-item-title dense class="white--text">{{
                item.title
              }}</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
          <v-list-group
            :key="item.title"
            v-else
            :group="item.path"
            color="indigo lighten-3"
            dense
          >
            <!-- this template is for the title of top-level items with children -->
            <template #activator>
              <v-list-item-icon>
                <v-icon color="white">{{ item.icon }}</v-icon>
              </v-list-item-icon>

              <v-list-item-content dense>
                <v-list-item-title class="white--text">{{
                  item.title
                }}</v-list-item-title>
              </v-list-item-content>
            </template>
            <!-- this template is for the children/sub-items (2nd level) -->
            <template v-for="subItem in item.childrens">
              <v-list-item class="ml-5" :key="subItem.title" :to="subItem.path">
                <v-list-item-icon class="mr-4">
                  <v-icon color="white">{{ subItem.icon }}</v-icon>
                </v-list-item-icon>
                <v-list-item-title class="white--text">
                  {{ subItem.title }}
                </v-list-item-title>
              </v-list-item>
            </template>
          </v-list-group>
        </template>
      </v-list>
    </v-navigation-drawer>

    <!--  HEADER-->
    <v-app-bar app id="header">
      <v-app-bar-nav-icon
        @click="drawer = !drawer"
        color="black"
      ></v-app-bar-nav-icon>

      <v-toolbar-title class="black--text"
        >SOLID WASTE MANAGEMENT SYSTEM</v-toolbar-title
      >
      <!-- <input v-model="userToken" type="text" /> -->
      <v-spacer></v-spacer>
      <v-divider vertical></v-divider>
      <div class="ml-5">{{ user_name }}</div>
      <v-btn icon>
        <v-icon @click="logout">mdi-logout-variant</v-icon>
      </v-btn>
    </v-app-bar>

    <v-main>
      <router-view></router-view>
    </v-main>
    <v-footer padless>
      <v-col class="text-center" cols="12">
        {{ new Date().getFullYear() }} — <strong>SWMS</strong>
      </v-col>
    </v-footer>
  </v-app>
</template>

<script>
var url = window.location;

let searchParams = new URLSearchParams(url.search);
let iisToken = searchParams.get("token");
let iisTokenID = searchParams.get("token_id");
let origin = searchParams.get("origin");

import { mapActions, mapGetters, mapMutations } from "vuex";
import swal from "sweetalert";
export default {
  data: () => ({
    token: iisToken,
    token_id: iisTokenID,
    origin: origin,
    drawer: null,
    user_name: "",
    adminLink: {
      children: "1",
      childrens: [
        {
          header_path: "/system",
          icon: "mdi-view-module",
          path: "/res",
          title: "User Type",
        },
        {
          header_path: "/system",
          icon: "mdi-account-settings",
          path: "/user",
          title: "User Management",
        },
        {
          header_path: "/system",
          icon: "mdi-image-filter-hdr",
          path: "/land",
          title: "Land Classification",
        },
        {
          header_path: "/system",
          icon: "mdi-domain",
          path: "/nature",
          title: "Nature of Business",
        },
      ],
      icon: "mdi-cog-outline",
      title: "System",
      to: "/system",
    },
  }),
  methods: {
    ...mapActions([
      "getIISToken",
      "getSidebar",
      "getSubSidebar",
      "getCRSToken",
      "loginAPI",
      "getSidebar2",
      "getBackendData",
    ]),
    ...mapMutations(["setSessionMutation"]),
    logout() {
      let origin = localStorage.getItem("originNum");
      swal({
        text: "Are you sure to end your session?",
        icon: "warning",
        buttons: true,
        dangerMode: true,
      }).then((willEnd) => {
        if (willEnd) {
          localStorage.clear();
          sessionStorage.clear();
          if (origin == "iis") {
            window.location.href = "https://iis.emb.gov.ph/embis/";
          } else if (origin == "crs") {
            window.location.href = "https://client.emb.gov.ph/crs";
          }
        }
      });
    },
    newAllLink() {
      let item = this.allLinks.map((e, i) => {
        let x = this.allSubLinks.filter((el, il) => e.to == el.header_path);
        return { ...e, childrens: x };
      });
      if (this.getStateSession["admin"] == 1) {
        item.push(this.adminLink);
      }

      return item;
    },
    getAllSideBar() {
      this.getSidebar({
        access_level: this.getStateSession["user_access_level"],
      }).then(() => {});
      this.getSubSidebar({
        access_level: this.getStateSession["user_access_level"],
      });
    },
    getFromOrigin() {
      var that = this; // USE THIS WHEN Using nested .then() to call function or state inside
      if (this.origin == "iis") {
        this.getIISToken({
          token: this.token,
          id: this.token_id,
          origin: this.origin,
        }).then(function (data) {
          that.loginAPI({
            tokens: data["employee_id"],
            origin: data["origin"],
          });
          // that.getSidebar2({
          //   access_level: sessionStorage.getItem("access"),
          //   token: data,
          // });
        });
      } else if (this.origin == "crs") {
        this.getCRSToken({
          company_id: this.token,
          user_id: this.token_id,
          origin: this.origin,
        }).then(function (data) {
          that.loginAPI({
            tokens: data["employee_id"],
            origin: data["origin"],
          });
        });
      }
    },
  },
  computed: mapGetters(["allLinks", "allSubLinks", "getStateSession"]),

  created() {
    // this.setSessionMutation(this.token); //USE MUTATION
    if (localStorage.getItem("ascwsktken")) {
      this.getBackendData({
        token: localStorage.getItem("ascwsktken"),
        origin: localStorage.getItem("originNum"),
      }).then(() => {
        this.getAllSideBar();
        this.user_name = this.getStateSession["name"];
      });
    } else {
      this.getFromOrigin();
    }
  },
};
</script>