const mutations = {
       allLinks: (state, User) => (state.links = User),
       allSubLinks: (state, User) => (state.sublinks = User),
       allPersonnel: (state, User) => (state.personnel = User),
       allGroup: (state, data) => (state.group = data),
       setSessionMutation: (state, data) => (state.sessionState = data),
}

export default {
       state,
       getters,
       actions,
       mutations
};