import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import vuetify from './plugins/vuetify'
import VueSweetalert2 from 'vue-sweetalert2';
import Vuelidate from 'vuelidate'
import VueHtmlToPaper from 'vue-html-to-paper';
import VueHtml2pdf from 'vue-html2pdf'
import VueCookies from 'vue-cookies';
import Vue2Editor from "vue2-editor";
// import { Field, Form } from 'vee-validate';
let basePath = window.location.origin;
import "./sass/style.scss"
import "./sass/create_plan_style.scss";
const options = {
  name: "EMED-ESWMS-F003 Rev. 1/10-15-2018",
  specs: ["fullscreen=yes", "titlebar=yes", "scrollbars=yes"],
  //styles: [`${basePath}/styles/create_plan_style.scss`],
  styles: [`${basePath}/swm/styles/create_plan_style.scss`],  // !!! FOR PRODUCTION !!!
  windowTitle: "EMED-ESWMS-F003 Rev. 1/10-15-2018"
};

Vue.mixin({
  methods: {
    globalFileChangeNameOthers: function (path, reference_number, new_name) {
      let regnum = reference_number.split("-");
      return `${path}/swms_api/public/typ_file/${regnum[0]}/${reference_number}/others/${new_name}`
    },
    globalFileChangeName: function (path, reference_number, new_name) {
      let regnum = reference_number.split("-");
      return `${path}/swms_api/public/typ_file/${regnum[0]}/${reference_number}/outline/${new_name}`
    },
    globalFileChangeNameForUpload: function (path, reference_number) {
      let regnum = reference_number.split("-");
      return `${path}/swms_api/public/typ_file/${regnum[0]}/${reference_number}/outline/`
    },
  },
})
Vue.config.productionTip = false
Vue.use(VueHtmlToPaper, options);
Vue.use(VueHtml2pdf)
Vue.use(Vuelidate)
Vue.use(VueCookies);
Vue.use(Vue2Editor);

new Vue({
  router,
  store,
  vuetify,
  VueSweetalert2,
  Vuelidate,
  VueHtml2pdf,
  VueCookies,
  Vue2Editor,
  render: h => h(App)
}).$mount('#app')
